/* whole area DASHBOARD */

.dashboard {
  margin: 0 auto;
  padding: 3% 10% 5% 10%;
  font-family: "Inter", sans-serif;
}

.dashboard h1 {
  margin: 0;
  padding: 0;
  font-size: 1.3rem;
}

.dashboard h3 {
  font-size: 0.8rem;
}

.dashboard-title {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.dashboard-title_title {
  margin-bottom: 10px;
}

/* @MEDIA  RESPONSIVENESS*/
@media (max-width: 576px) {
  .dashboard-title {
    flex-direction: column;
  }

  .dashboard-title_title {
    width: 100%;
    border-bottom: 1px solid hsl(228, 12%, 44%);
  }

  .dashboard.dashboard_dark {
    background: linear-gradient(
      to bottom,
      hsl(232, 19%, 15%) 15%,
      hsl(230, 17%, 14%) 15%
    );
  }

  .dashboard.dashboard_light {
    background: linear-gradient(
      to bottom,
      hsl(225, 100%, 98%) 10%,
      hsl(0, 0%, 100%) 10%
    );
  }
}

/* Dark Theme */

.dashboard_dark {
  background: linear-gradient(
    to bottom,
    hsl(232, 19%, 15%) 30%,
    hsl(230, 17%, 14%) 30%
  );
}

.card_dark {
  background-color: hsl(228, 28%, 20%);
}

.dashboard_dark_maintext {
  color: hsl(0, 0%, 100%);
}

.dashboard_dark_secondarytext {
  color: hsl(228, 34%, 66%);
}

/* Light Theme */

.dashboard_light {
  background: linear-gradient(
    to bottom,
    hsl(225, 100%, 98%) 30%,
    hsl(0, 0%, 100%) 30%
  );
}

.card_light {
  background-color: hsl(227, 47%, 96%);
}

.dashboard_light_maintext {
  color: hsl(230, 17%, 14%);
}

.dashboard_light_secondarytext {
  color: hsl(228, 12%, 44%);
}

/* -------------------------TOTAL---------------------------- */
.total {
  width: 100%;
  position: relative;
  z-index: 1;
  margin-bottom: 40px;
}

.total-grid {
  display: grid;
  align-items: center;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 25px;
  max-width: 100%;
}

.totalblock {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  padding: 1rem 1.3rem;
  border-radius: 4px;
}

/* borders */
.totalblock-fb {
  border-top: 4px solid hsl(208, 92%, 53%);
}

.totalblock-twtr {
  border-top: 4px solid hsl(203, 89%, 53%);
}

.totalblock-yt {
  border-top: 4px solid hsl(348, 97%, 39%);
}

/* special block for instagram --> */
.totalblock-insta {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* margin: auto; */
  position: relative;
  padding: 1rem 1.3rem;
  box-sizing: border-box;
  background-clip: padding-box;
  border-top: solid 4px transparent;
  border-radius: 4px;
}
.totalblock-insta:before {
  content: " ";
  position: absolute;
  height: 10px;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin-top: -4px;
  border-radius: inherit;
  background: linear-gradient(90deg, hsl(37, 97%, 70%), hsl(329, 70%, 58%));
  z-index: -1;
}

/* --> special block for instagram */

.totalblock_name {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 0 15px 0;
  font-size: 8pt;
}

.totalblock_name span {
  padding: 0 0 0 10px;
}

.totalBlock-mainvalue {
  font-size: 38pt;
  font-weight: 700;
}

.totalblock-followers {
  margin: 0 0 15px 0;
  font-size: 8pt;
  letter-spacing: 2pt;
  font-weight: 700;
}

.trend {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 9pt;
}

/* ------------------------OVERVIEW-------------------------------- */
.overview {
  width: 100%;
}

.overview h2 {
  margin: 0 0 1.5rem 0;
  font-size: 1.1rem;
}

.overview-grid {
  display: grid;
  align-items: center;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-template-rows: repeat(2, 1fr);
  grid-auto-flow: column;
  grid-gap: 20px 25px;
  max-width: 100%;
}

@media (max-width: 980px) {
  .overview-grid {
    grid-template-rows: repeat(4, 1fr);
  }
}

.overviewblock {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: inherit;
  align-items: center;
}

.subblock {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  padding: 1rem;
  border-radius: 5px;
  gap: 10px;
  align-items: center;
}

.overviewblock-views-title_text,
.overviewblock-likes-title_text,
.overviewblock-views-data_value,
.overviewblock-likes-data_value {
  justify-self: start;
}

.overviewblock-views-title_text,
.overviewblock-likes-title_text {
  font-size: 10pt;
}

.overviewblock-views-data_value,
.overviewblock-likes-data_value {
  font-size: 20pt;
  font-weight: 700;
}

.overviewblock-views-title_icon,
.overviewblock-likes-title_icon,
.overviewblock-views-data_trend,
.overviewblock-likes-data_trend {
  justify-self: end;
}

/* ---------------------------ICONS------------------------------------- */
.icon-fb {
  background: url(/src/images/icon-facebook.svg) no-repeat;
}
.icon-insta {
  background: url(/src/images/icon-instagram.svg) no-repeat;
}
.icon-twtr {
  background: url(/src/images/icon-twitter.svg) no-repeat;
}
.icon-yt {
  background: url(/src/images/icon-youtube.svg) no-repeat;
}

.icon {
  display: inline-block;
  width: 20px;
  height: 20px;
}

.icon-up {
  background: url(/src/images/icon-up.svg) no-repeat center;
}
.icon-down {
  background: url(/src/images/icon-down.svg) no-repeat center;
}

.up-color {
  color: hsl(163, 72%, 41%);
}

.down-color {
  color: hsl(356, 69%, 56%);
}

.attribution {
  padding: 10px;
  text-align: center;
  background: linear-gradient(90deg, hsl(37, 97%, 70%), hsl(329, 70%, 58%));
}
