/* container = div>label>input, slide>before: */
.toggle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0 0 0;
  font-size: 10pt;
  font-weight: bold;
  color: hsl(228, 34%, 66%);
}

/* MEDIA */
@media (max-width: 576px) {
  .toggle {
    width: 100%;
    padding: 0;
    margin-bottom: 20px;
  }
}

/* The switch - the Label around the slider */
.switch {
  position: relative;
  display: inline-block;
  margin-left: 15px;
  width: 39px;
  height: 19px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider = span */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-transition: 0.4s;
  transition: 0.5s;
}

/* themes */
.dark {
  background: linear-gradient(90deg, hsl(210, 78%, 56%), hsl(146, 68%, 55%));
}

.light {
  background: hsl(230, 22%, 74%);
}

.slider.dark:before {
  background-color: hsl(228, 28%, 20%);
}

.slider.light:before {
  background-color: hsl(0, 0%, 100%);
}

/* the ball inside slider */
.slider:before {
  position: absolute;
  content: "";
  height: 15px;
  width: 15px;
  left: 1px;
  bottom: 2px;
  -webkit-transition: 0.4s;
  transition: 0.5s;
}

/* moving :before ball when hided input checked */
input:checked + .slider:before {
  -webkit-transform: translateX(20px);
  -ms-transform: translateX(20px);
  transform: translateX(20px);
}

/* input:focus + slider {
  box-shadow: 10px 10px 10px #c6d7e6;
} */

.slider.round {
  border-radius: 26px;
}
.slider.round:before {
  border-radius: 50%;
}
